import React from "react";
import { Navbar, Nav, Container, Row } from "react-bootstrap";
import {Link} from 'react-router-dom';
import Logo from "../../assets/logo.png";
import "./style.css";
const Header = () => {
  return (
    <>
      <Container fluid className="sticky-top">
        <Row className="line"></Row>
        <Row>
          <Navbar variant="light" className="navBar">
            <Nav className="line  "></Nav>
            <Navbar.Brand href="/" className=" flex-grow-1 m-3 p-2">
              <img
                className="logo"
                alt=""
                src={Logo}
                width="100"
              />{" "}
            </Navbar.Brand>
            <Nav className="ml-auto d-fllex d-flex justify-content-between">
              <Nav.Link className="navLinks " href="/">
                Home
              </Nav.Link>
              <Nav.Link className="navLinks" >
              <Link to="/about">About</Link>
              </Nav.Link>
              <Nav.Link className="navLinks" >
              <Link to="/signin">Registration</Link>
              </Nav.Link>
              <Nav.Link className="navLinks" >
              {/* <Link to="/contact">Contact</Link> */}
              Contact
              </Nav.Link>
              <Nav.Link
                className="navLinks"
                href="https://www.youtube.com/channel/UCQUW5mf-ygF_B3Egf1uorug"
              >
                Youtube
              </Nav.Link>
            </Nav>
          </Navbar>
        </Row>
      </Container>
    </>
  );
};

export default Header;
