import React from "react";
import { Card, ListGroupItem, ListGroup } from "react-bootstrap";
import "./style.css";
const CardCust = (props) => {
  return (
    <Card style={{ width: "18rem" }}>
      <Card.Img variant="top" src={props.cardPic} />
      <Card.Body>
        <Card.Title>{props.cardTitle}</Card.Title>
        <Card.Text>{props.cardText}</Card.Text>
      </Card.Body>
      <ListGroup className="list-group-flush">
        {/* <ListGroupItem>Cras justo odio</ListGroupItem> */}
        {props.listGroupItems}
      </ListGroup>
      <Card.Body>
        {/* <Card.Link href="#">Card Link</Card.Link> */}
        {props.cardLink}
      </Card.Body>
    </Card>
  );
};

export const UserCard = (props) => {
  return (
    <>
      <Card style={{ border: "none" }}>
        <Card.Body>
          <Card.Img
            src={props.imgSrc}
            style={{ borderRadius: "50%" }}
            className=" mx-auto d-block w-75 p-1 m-1"
          />
          <Card.Title className="text-center">{props.Title}</Card.Title>
          <Card.Subtitle className="mb-2 text-center text-muted">{props.SubTitle}</Card.Subtitle>
          <Card.Text className="text-center fs-6 fw-normal">{props.Text}</Card.Text>
        </Card.Body>
      </Card>
    </>
  );
};
export default CardCust;
