import React from "react";
import { Card, Container, Form, Button } from "react-bootstrap";
import Header from "../../../components/Header/Header";
import {Link} from 'react-router-dom';
import Footer from "../../../components/Footer/Footer";
const Signup = () => {
  return (
    <>
      <Header />
      <Container>
        <Card style={{ width: "25rem" }} className="m-3 p-2">
          <Card.Body>
            <Card.Title className="text-center"> Registration</Card.Title>
            <Card.Text>
              <Form>
                <Form className=" p-2">
                  <Form.Group controlId="formBasicEmail" className="mt-2">
                    <Form.Label className="text-muted">First Name</Form.Label>
                    <Form.Control type="text" placeholder="Enter First Name" />
                  </Form.Group>
                  <Form.Group controlId="formBasicEmail" className="mt-2">
                    <Form.Label className="text-muted">Last Name</Form.Label>
                    <Form.Control type="text" placeholder="Enter Last Name" />
                  </Form.Group>
                  <Form.Group controlId="formBasicEmail" className="mt-2">
                    <Form.Label className="text-muted">
                      Email address
                    </Form.Label>
                    <Form.Control type="email" placeholder="Enter email" />
                  </Form.Group>

                  <Form.Group controlId="formBasicPassword" className="mt-2">
                    <Form.Label className="text-muted">Password</Form.Label>
                    <Form.Control type="password" placeholder="Password" />
                  </Form.Group>

                  <Form.Group controlId="formBasicEmail" className="mt-2">
                    <Form.Label className="text-muted">
                      Mobile Number
                    </Form.Label>
                    <Form.Control type="text" placeholder="Enter Mobile Number" />
                  </Form.Group>
                  <Form.Group controlId="formBasicEmail" className="mt-2">
                    <Form.Label className="text-muted">
                      College Name
                    </Form.Label>
                    <Form.Control type="email" placeholder="Enter College Name" />
                  </Form.Group>
                  <Button
                    variant="primary"
                    type="submit"
                    className="d-block mt-3"
                  >
                    Register
                  </Button>
                </Form>
              </Form>
            </Card.Text>
            <Card.Text>
              Have an Account? <Card.Link ><Link to="/signin">Signin</Link></Card.Link>
            </Card.Text>
          </Card.Body>
        </Card>
      </Container>
      <Footer />
    </>
  );
};

export default Signup;
