import React from "react";
import { Card, Container, Form,Button} from "react-bootstrap";
import Header from '../../../components/Header/Header';
import Footer from '../../../components/Footer/Footer';
import {Link} from 'react-router-dom';
const Signin = () => {
  return (
    <>
    <Header />
    <Container >
      <Card style={{width:"25rem"}} className="m-3 p-2"> 
        <Card.Body>
          <Card.Title className="text-center"> Login To Sambhav Law</Card.Title>
          <Card.Text>
            <Form>
              <Form className='g-3 p-2'>
                <Form.Group controlId="formBasicEmail">
                  <Form.Label className="text-muted">Email address</Form.Label>
                  <Form.Control type="email" placeholder="Enter email" />
                  
                </Form.Group>

                <Form.Group controlId="formBasicPassword" className="mt-2">
                  <Form.Label  className="text-muted">Password</Form.Label>
                  <Form.Control type="password" placeholder="Password" />
                </Form.Group>
                <Button variant="primary" type="submit" className="d-block mt-3">
                  Submit
                </Button>
              </Form>
            </Form>
            
          </Card.Text>
          <Card.Text>Dont Have a Account?{' '}
          <Card.Link ><Link to="/signup">SignUp</Link></Card.Link>
          </Card.Text>
        </Card.Body>
      </Card>
    </Container>
    <Footer/>
    </>
  );
};

export default Signin;
